.container {
  display: flex;
  flex-direction: column;
}

.header {
  font-weight: bold;
  margin-block: 0.25rem;
  font-size: 1.15rem;
}

.inputGroup {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-gap: 5px;
  align-items: center;
}

@media(max-width: 700px) {
  .container {
    align-items: center;
  }
}


.label {
  text-align: right;
}

.label:after {
  content: ":";
}