@mixin reset-button {
  background-color: transparent;
  border-width: 0;
  font-family: inherit;
  font-size: inherit;
  font-style: inherit;
  font-weight: inherit;
  line-height: inherit;
  padding: 0;
}

.showCodeBtn {
  @include reset-button;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.15rem;
  font-weight: bold;
}

.showCodeBtn:hover .text {
  text-decoration: underline;
}

.showCodeBtn:hover {
  cursor: pointer;
}

.iconContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes toHide {
  from {
    transform: rotate(0.25turn);
  }

  to {
    transform: rotate(0turn);
  }
}

@keyframes toShow {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(0.25turn);
  }
}

.enterActive {
  rotate: 0turn
}

.enterDone {
  transition: rotate 50ms ease-in;
  rotate: 0.25turn;
}

.exitActive {
  rotate: 0.25turn
}

.exitDone {
  transition: rotate 50ms ease-in;
  rotate: 0turn;
}