.container {
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.title {
  font-size: 1.5rem;
  text-align: center;
  margin-bottom: 0.5rem;
}

.task {
  font-size: 1.25rem;
  text-align: center;
}

.text p {
  text-indent: 2rem;
  margin-block: 0.5rem;
}

.formula {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-block: 0.5rem;
}

@media ((max-width: 388px) and (min-width: 354px)) {
  .title {
    font-size: 1.35rem;
  }

  .task {
    font-size: 1.15rem;
  }
}

@media ((max-width: 400px) and (max-height: 900px)) {
  .container {
    min-height: 125vh;
  }
}